    methods: {
        handleScroll(){

        },
        getRecipeList() {
            this.$vux.loading.show({
                text: 'Loading'
            })
           
            this.$fetch.post("fruits/app/recipe/getRecipeList",this.form).then(res =>{
                console.log(res,999);
                 this.$vux.loading.hide();
               
                if(res.obj.length == 0){
                    this.isUnMore1 = true;
                    return
                }else{
             alert(data.msg)
          }
                this.$nextTick(()=>{
                    res.obj.forEach(e => {
                        this.bookList.push(e);
                    });
                })
                
            
            })
        },
         selPullUp () {
             this.form.current++;
            this.$nextTick(()=>{
                    this.getRecipeList();
            })
           
           
            console.log('上拉刷新数据')

        },
        pulldown(){
            this.bookList = [];
            this.isUnMore1 = false;
            this.form.current = 1;
            this.getRecipeList();
        },
        input(){
            if(this.timer){
                clearTimeout(this.timer)
                this.timer ="";
            }
            this.timer = setTimeout(() =>{
                this.form.name = this.iptVal;
                this.form.current = 1;
                this.getRecipeList();
            },1000);
        },
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
        this.getRecipeList();
    },
}



